.site-list {
    margin: 0;
    padding: 0;
    list-style-type: none;
    background-color: $blue;
    text-align: center;
    
    @media(min-width: $tablet) {
        background:none;
        display: grid;
       grid-template-columns: repeat(5,1fr);
    }
}

.site-list__item {
    border-bottom: 1px solid rgba(1,1,1, 0.20);
    border-top: 1px solid rgba(255,255,255, 0.20);

    @media(min-width: $tablet) {
       padding: 0;
       border: none;

       &:nth-child(3) {
        grid-column: 4/5;
       }
    }
}

.site-list__item:last-child {
    border-bottom: none;
    box-shadow: inset 0px -10px 15px -10px rgba(0,0,0,0.20);
    
    @media(min-width: $tablet) {
        box-shadow: none;
    }
}

.site-list__item:first {
    border-top: none;
}

.site-list__link {
    text-decoration: none;
    color: $white;
    text-transform: uppercase;
    font-size: 18px;
    line-height: 26px;
    font-weight: bold;
    display: block;
    padding: 17px 5px 13px 5px;
    cursor: pointer;
    transition: all .2s ease-in;

    &:hover {
        background-color: #669EC0;
    }
    &:active {
        background-color: #5496BD;
        color: rgba(255,255,255, .3);
    }


    @media(min-width: $tablet) {
        &:hover {
            color: #766357;
            background-color: transparent;
        }

    }
    

    @media(min-width: $tablet) {
        font-size: 14px;
        line-height: 21px;
        color: $black;

        &.active {
            color: #766357;
        }

    }

}

